const { InfoBox } = require('react-google-maps/lib/components/addons/InfoBox');
const demoFancyMapStyles = require('../../static/map.json');

const StyledMapWithAnInfoBox = compose(
	withProps({
		googleMapURL:
			'https://maps.googleapis.com/maps/api/js?key=AIzaSyDXtYvuzlmYuu75IkV-lZXDmdiXH28i_GA&libraries=geometry,drawing,places',
		loadingElement: <div style={{ height: `100%` }} />,
		containerElement: <div style={{ height: `600px` }} />,
		mapElement: <div style={{ height: `100%` }} />,
		center: { lat: 48.8721, lng: 2.3047669 }
	}),
	withStateHandlers(
		() => ({
			isOpen: false
		}),
		{
			onToggleOpen: ({ isOpen }) => () => ({
				isOpen: !isOpen
			})
		}
	),
	withScriptjs,
	withGoogleMap
)(props => (
  <GoogleMap
    defaultZoom={18}
    defaultCenter={props.center}
    defaultOptions={{ styles: demoFancyMapStyles }}
  >
    <InfoBox
      defaultPosition={
				new google.maps.LatLng(props.center.lat, props.center.lng)
			}
      options={{ closeBoxURL: ``, enableEventPropagation: true }}
    >
      <div className="googlmapdiv">
        <p className="googlemaptitle">EatMeat</p>
      </div>
    </InfoBox>
    <Marker
      position={{ lat: 22.6273, lng: 120.3014 }}
      onClick={props.onToggleOpen}
    >
      {props.isOpen && (
      <InfoBox
        onCloseClick={props.onToggleOpen}
        options={{ closeBoxURL: ``, enableEventPropagation: true }}
      >
        <div className="googlmapdiv">
          <p className="googlemaptitle">EatMeat</p>
        </div>
      </InfoBox>
			)}
    </Marker>
  </GoogleMap>
));

